import BaseApiService from "../../BaseApiService";

const DEFAULT_PATH = "/custom/api/deviation";

class ProductDeviationService extends BaseApiService {
  getAll = (start = 0, limit = 10, search = "") => {
    const url = `${DEFAULT_PATH}/search?&start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };
  getDeviationCategoryInfo = (uid: string) => {
    const url = `${DEFAULT_PATH}/manage/uid/${uid}`;
    return this.makeGetRequestWithAuth(url);
  };
  save = (obj: any) => {
    const url = `${DEFAULT_PATH}/manage`;
    return this.makePostRequestWithAuth(url, obj);
  };

  update = (uid: string | undefined, obj: any) => {
    const url = `${DEFAULT_PATH}/manage/${uid}`;
    return this.makePutRequestWithAuth(url, obj);
  };

  getAllDeviationCategory = (start = 0, limit = 10, search = "") => {
    const url = `/custom/api/dev/cat/approval/func/search?${search}start=${start}&limit=${limit}`;
    return this.makeGetRequestWithAuth(url);
  };
  
  findById = (id: string) => {
    const url = `${DEFAULT_PATH}/uid/${id}`;
    return this.makeGetRequestWithAuth(url);
  };
  activate= (uid: string) =>{
    const url = `${DEFAULT_PATH}/activate/${uid}`;
    return this.makePutRequestWithAuth(url);
  }
  Inactivate= (uid: string) =>{
    const url = `${DEFAULT_PATH}/inactivate/${uid}`;
    return this.makePutRequestWithAuth(url);
  }

  modelToAPIParam = () => {
    console.log("object");
    
  };
}

export default new ProductDeviationService();
